.container__rating-state-start {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.rating-state-start {
  margin: 0 2rem;
  padding: 2rem;
  background-color: hsl(213deg, 19%, 18%);
  border-radius: 1rem;
}
@media (min-width: 1440px) {
  .rating-state-start {
    width: 37.1rem;
  }
}
.rating-state-start .start {
  width: 4rem;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2d3743;
  border-radius: 50%;
}
.rating-state-start .start img {
  width: 1.5rem;
  height: 1.5rem;
}
.rating-state-start .question {
  color: hsl(0deg, 0%, 100%);
  letter-spacing: 0.1rem;
}
.rating-state-start .description {
  color: hsl(216deg, 12%, 54%);
  font-size: 1.5rem;
  line-height: 1.5;
}
.rating-state-start .rating {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
}
.rating-state-start .rating .number {
  width: 4.5rem;
  height: 4.5rem;
  display: grid;
  place-content: center;
  background-color: #2d3743;
  color: hsl(216deg, 12%, 54%);
  border-radius: 50%;
  border: none;
  font-size: 1.8rem;
  cursor: pointer;
}
.rating-state-start .rating .number:hover {
  background-color: hsl(217deg, 12%, 63%);
  color: hsl(0deg, 0%, 100%);
}
.rating-state-start .rating .number:focus {
  background-color: hsl(25deg, 97%, 53%);
  color: hsl(0deg, 0%, 100%);
}
.rating-state-start .btn-submit {
  width: 100%;
  background: hsl(25deg, 97%, 53%);
  border: none;
  text-transform: uppercase;
  color: hsl(0deg, 0%, 100%);
  padding: 1rem 0;
  border-radius: 2.5rem;
  letter-spacing: 0.1rem;
  cursor: pointer;
}
.rating-state-start .btn-submit:hover {
  color: hsl(25deg, 97%, 53%);
  background-color: hsl(0deg, 0%, 100%);
}

.active {
  background-color: hsl(25deg, 97%, 53%);
  color: hsl(0deg, 0%, 100%);
}

.container__thankYou-state-start {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
}

.thankYou-state-start {
  margin: 0 2rem;
  padding: 2rem;
  background-color: hsl(213deg, 19%, 18%);
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1440px) {
  .thankYou-state-start {
    width: 37.5rem;
  }
}
.thankYou-state-start .selected {
  background-color: #2d3743;
  padding: 1rem;
  margin: 2rem 0;
  color: hsl(25deg, 97%, 53%);
  border-radius: 5rem;
  font-size: 1.5rem;
}
.thankYou-state-start .thankYou {
  color: hsl(0deg, 0%, 100%);
  font-size: 2.5rem;
}
.thankYou-state-start .goodBye {
  color: hsl(216deg, 12%, 54%);
  font-size: 1.5rem;
  text-align: center;
  margin-bottom: 3rem;
}